import React from "react";
import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import key from "../../../util/key";
import Layout4 from "../../../util/Layout4";
import { Tree, Typography, Alert } from "antd";
import Context from "../../../Context";
import { ProfileOutlined } from "@ant-design/icons";
import paramsHasChanged from "../../../util/paramsHasChanged";
import Title from "../Title";
import ReferenceDetail from "./ReferenceDetail";

const { Text } = Typography;
const config = window.appConfig || {};

class Reference extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      treeSearch: "",
      treeKey: key(),
      tree: [],
    };
  }

  pendingStart = () => {
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const { param1: refId } = this.props.match.params;
    this.setState({
      refId,
      treeKey: key(),
    });
  };

  load = async () => {
    this.pendingStart();
    let search = [
      {
        field: "code",
        valarr: Object.keys(config.adminReferences) || [],
      },
    ];
    if (this.state.treeSearch) {
      search.push({
        field: "label",
        compare: "IKE",
        value: `%${this.state.treeSearch}%`,
      });
    }
    let tree = await this.context.api.q(
      "/workflow/class/list",
      { search },
      { toTree: true }
    );
    this.setState({
      tree: tree || [],
      treeKey: key(),
    });
    this.pendingEnd();
  };

  getItem = () => {
    const { refId, tree } = this.state;
    return tree.find((item) => item.id === refId) || {};
  };

  onTreeSelect = (selectedKeys) => {
    if (Array.isArray(selectedKeys)) {
      if (selectedKeys[0]) {
        this.props.history.push(`/admin/reference/${selectedKeys[0]}`);
      }
    }
  };
  onTreeSearch = (value) => {
    this.setState({ treeSearch: value }, () => this.load());
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.reference." + key });
  };

  render() {
    const { pending, treeKey, refId } = this.state;
    const { intl } = this.props;
    const item = this.getItem();
    return (
      <>
        <Title
          title={this.m("reference")}
          url="/admin/reference"
          icon={<ProfileOutlined />}
        />
        <Layout4 pending={pending} onSearch={this.onTreeSearch}>
          <div>
            {item.id && (
              <>
                <Text type="secondary">{item.label}</Text>
              </>
            )}
          </div>
          <div className="isoNoteBtns"></div>
          <Tree
            key={treeKey}
            defaultExpandAll
            defaultExpandParent
            defaultSelectedKeys={[refId]}
            defaultExpandedKeys={[refId]}
            treeData={this.state.tree}
            onSelect={this.onTreeSelect}
          />

          <div>
            {refId && <ReferenceDetail key={item?.id} reference={item} />}
          </div>
        </Layout4>
      </>
    );
  }
}

export default withRouter(injectIntl(Reference));
